import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router'
import { useUserInfoStore } from '@/store/user-data'

const routes = [
  {
    path: '/user',
    component: () => import('../views/user/User.vue'),
    children: [
      {
        path: 'login',
        name: 'login',
        component: () => import('../views/user/Login.vue'),
        alias: '/login',
      },
      {
        path: 'register',
        name: 'register',
        component: () => import('../views/user/Register.vue'),
        alias: '/register',
      },
      {
        path: 'forgetPwd',
        name: 'forgetPwd',
        component: () => import('../views/user/ForgetPwd.vue'),
        alias: '/forgetPwd',
      },
    ],
  },
  {
    path: '/',
    component: () => import('../views/main/Main.vue'),
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('../views/main/Home.vue'),
      },
      {
        path: 'story',
        component: () => import('../views/main/story/Story.vue'),
        children: [
          {
            path: 'list',
            name: 'list',
            component: () => import('../views/main/story/List.vue'),
          },
          {
            path: 'content',
            name: 'content',
            component: () => import('../views/main/story/Content.vue'),
          },
          {
            path: 'edit',
            name: 'edit',
            component: () => import('../views/main/story/Edit.vue'),
          },
          {
            path: 'charList',
            name: 'charList',
            component: () => import('../views/main/story/CharList.vue'),
          },
          {
            path: 'charContent',
            name: 'charContent',
            component: () => import('../views/main/story/CharContent.vue'),
          },
          {
            path: 'charReview',
            name: 'charReview',
            component: () => import('../views/main/story/ReviewContent.vue'),
          },
        ],
      },
      {
        path: 'history',
        component: () => import('../views/main/history/History.vue'),
        children: [
          {
            path: 'list',
            name: 'list',
            component: () => import('../views/main/history/List.vue'),
          },
          {
            path: 'record',
            name: 'record',
            component: () => import('../views/main/history/Record.vue'),
          },
        ],
      },
      {
        path: 'chat',
        name: 'chat',
        component: () => import('../views/main/Chat.vue'),
      },
      {
        path: 'gallery',
        name: 'gallery',
        component: () => import('../views/main/Gallery.vue'),
      },
      {
        path: 'setting',
        name: 'setting',
        component: () => import('../views/main/Setting.vue'),
      },
      {
        path: 'contact',
        name: 'contact',
        component: () => import('../views/main/Contact.vue'),
      },
    ],
  },
  {
    path: '/admin',
    component: () => import('../views/admin/Admin.vue'),
    children: [
      {
        path: 'story',
        component: () => import('../views/admin/myStory/Story.vue'),
        children: [
          {
            path: 'list',
            name: 'list',
            component: () => import('../views/admin/myStory/List.vue'),
          },
          {
            path: 'content',
            name: 'content',
            component: () => import('../views/admin/myStory/Content.vue'),
          },
          {
            path: 'edit',
            name: 'edit',
            component: () => import('../views/admin/myStory/Edit.vue'),
          },
        ],
      },
      {
        path: 'charater',
        component: () => import('../views/admin/myChar/Char.vue'),
        children: [
          {
            path: 'list',
            name: 'list',
            component: () => import('../views/admin/myChar/List.vue'),
          },
          {
            path: 'create',
            name: 'create',
            component: () => import('../views/admin/myChar/Create.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/error',
    name: 'error',
    component: () => import('../views/ErrorPage.vue'),
  },
  {
    path: '/:catchAll(.*)',
    name: 'not-found',
    redirect: '/error',
  },
]
if (import.meta.env.VITE_APP_MODE !== 'production') {
  routes.push({
    path: '/test',
    name: 'test',
    component: () => import('../views/TestPage.vue'),
  })
}

const router = createRouter({
  // history: createWebHistory('/'),
  history: createWebHashHistory(),
  routes,
})

router.beforeEach(async (to) => {
  const userInfoStore = useUserInfoStore()
  if (userInfoStore.accessToken && to.name === 'login') {
    return { name: 'home' }
  } else if (
    // 检查用户是否已登录
    !userInfoStore.accessToken &&
    // ❗️ 避免无限重定向
    !['login', 'register', 'forgetPwd'].includes(to.name as string)
  ) {
    // 将用户重定向到登录页面
    return { name: 'login' }
  }
})

export default router
