import { ElMessage, ElMessageBox } from 'element-plus'

export enum ToastType {
  Warning = 'warning',
  Success = 'success',
  Error = 'error',
  Info = 'info',
}
const warningImg =
  'data:image/png;base64,'
const errorImg =
  'data:image/png;base64,'
const successImg =
  'data:image/png;base64,'
const infoImg =
  'data:image/png;base64,'

const getTypeImg = (type: ToastType) => {
  switch (type) {
    case ToastType.Warning:
      return warningImg
    case ToastType.Success:
      return successImg
    case ToastType.Error:
      return errorImg
    case ToastType.Info:
      return infoImg
  }
}
const Message = {
  toast: (message: string, type: ToastType = ToastType.Success, duration: number = 3000) => {
    ElMessage({
      customClass: 'ahab-message',
      dangerouslyUseHTMLString: true,
      message: `<div class="ahab-message__content"><img class="ahab-message-icon" src="${getTypeImg(
        type,
      )}" /><span class="ahab-message-content">${message}</span></div>`,
      type,
      duration,
    })
  },
  warning: (message: string, duration: number = 3000) => {
    Message.toast(message, ToastType.Warning, duration)
  },
  error: (message: string, duration: number = 3000) => {
    Message.toast(message, ToastType.Error, duration)
  },
  success: (message: string, duration: number = 3000) => {
    Message.toast(message, ToastType.Success, duration)
  },
  info: (message: string, duration: number = 3000) => {
    Message.toast(message, ToastType.Info, duration)
  },
}
const MessageBox = ElMessageBox
export default Message
export { Message, MessageBox }
