import { USERINFO } from '@/store/user-data'
const USERINFO_KEY = 'AHAB_USERINFO'
const MGR_KEY = 'AHAB_MGR_USERINFO'

/**
 * 获取用户信息
 * @param key 用户信息对应 localStorage 中的key
 */
const getUserInfo = (fr?: string) => {
  try {
    const key = fr === 'mgr' ? MGR_KEY : USERINFO_KEY
    const userinfoJson = localStorage.getItem(key)
    return userinfoJson ? JSON.parse(userinfoJson) : {}
  } catch (error) {
    console.log('用户信息获取失败', error)
  }
}

const setUserInfo = (userInfo: USERINFO) => {
  try {
    localStorage.setItem(USERINFO_KEY, JSON.stringify(userInfo))
  } catch (error) {
    console.log('用户信息解析失败', error)
  }
}

/**
 * 获取用户token
 * @returns token
 */
const getUserToken = (fr?: string) => {
  const userinfo = getUserInfo(fr)
  return userinfo.accessToken
}

const clearUserinfo = (fr?: string) => {
  const key = fr === 'mgr' ? MGR_KEY : USERINFO_KEY
  localStorage.removeItem(key)
}

export { clearUserinfo, getUserInfo, getUserToken, setUserInfo }
