import { defineStore } from 'pinia'
import { getUserInfo, setUserInfo, clearUserinfo } from '@/utils/userinfo'
import { userInfoApi } from '@/api/user'
import Message from '@/utils/message'
export interface USERINFO {
  accessToken: string
  avatar: string
  expiresIn: number
  id: number
  nickname: string
  gender: string
  email: string
}

let userInfo = {} as USERINFO
try {
  userInfo = getUserInfo('') as USERINFO
} catch (error) {
  console.log('获取身份缓存error:', error)
}

export const useUserInfoStore = defineStore({
  id: 'user-data',
  state: () => ({
    accessToken: userInfo.accessToken || '',
    avatar: userInfo.avatar,
    nickname: userInfo.nickname,
    gender: userInfo.gender,
    email: userInfo.email,
    id: userInfo.id,
    role: '1', // 0 后台 1 前台
  }),
  getters: {
    isLogined: (state) => !!state.accessToken,
    isMgr: (state) => state.role === '0',
  },
  actions: {
    initUserInfo(userInfo: USERINFO) {
      setUserInfo(userInfo)
      this.accessToken = userInfo.accessToken
      this.avatar = userInfo.avatar
      this.id = userInfo.id
      this.nickname = userInfo.nickname
      this.gender = userInfo.gender
      this.email = userInfo.email
    },
    handleLogout() {
      this.accessToken = ''
      this.avatar = ''
      this.id = 0
      this.nickname = ''
      this.gender = ''
      this.email = ''
      clearUserinfo()
    },
    async getUserInfo() {
      try {
        const res: any = await userInfoApi()
        if (res.statusCode === 200) {
          const userInfo = { accessToken: this.accessToken, ...res.data }
          this.initUserInfo(userInfo)
        }
      } catch (error: any) {
        Message.toast(error.message)
      }
    },
  },
})
