import axios from 'axios'
import Message from '@/utils/message'
import { clearUserinfo } from '@/utils/userinfo'
import { useUserInfoStore } from '@/store/user-data'
// 设置全局axios默认值
axios.defaults.timeout = 15000 // 15秒的超时验证
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'

// 创建一个axios实例
const instance = axios.create({
  baseURL: import.meta.env.VITE_APP_API_HOST,
})
console.log(import.meta.env.VITE_APP_API_HOST, import.meta.env.MODE, '========')
instance.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'

axios.interceptors.request.use = instance.interceptors.request.use

// request拦截器
instance.interceptors.request.use(
  (config) => {
    const token = useUserInfoStore().accessToken

    if (token) {
      config.headers!.Authorization = `Bearer ${token}`
    }

    if (config.url?.indexOf('https') === 0) {
      config.baseURL = ''
    }
    return config
  },
  (err) => Promise.reject(err),
)

// respone拦截器
instance.interceptors.response.use(
  (response) => {
    let res
    const { config } = response
    if (config.responseType === 'blob') {
      res = response
    } else {
      res = response.data
    }
    return res
  },
  (error) => {
    if (error.code === 'ECONNABORTED') {
      Message.warning('请求超时，请稍后再试！')
      return Promise.reject(error.response)
    }

    if (error.message === 'Network Error') {
      Message.warning('网络错误，请检查网络')
      return Promise.reject(error.response)
    }

    if (error.response) {
      const { status } = error.response
      if (status === 401) {
        clearUserinfo()
      }
    }
    return Promise.reject(error.response)
  },
)

export default instance
