import axios from './index'

export function registerApi(params: any) {
  return axios({
    method: 'post',
    url: 'auth/register',
    data: params,
  });
}

export function getValidCodeApi(params: any) {
  return axios({
    method: 'get',
    url: 'auth/requestValidCode',
    params,
  })
}

export function userLoginApi(params: any) {
  return axios({
    method: 'post',
    url: 'auth/login',
    data: params,
  })
}

export function userResetPwdApi(params: any) {
  return axios({
    method: 'post',
    url: 'auth/resetPassword',
    data: params,
  })
}

export function userInfoApi() {
  return axios({
    method: 'get',
    url: 'user/profile'
  })
}